import React from "react";
import Helmet from "react-helmet";
import { graphql } from "gatsby";
import Head from "../components/head";
import Intro from "../components/intro";
import AboutMe from "../components/aboutMe";
import MainCaseStudy from "../components/mainCaseStudy";
import CaseStudy from "../components/caseStudy";

export default ({ data }) => (
    <main role="main" id="top">
        <Head />

        <Helmet
            bodyAttributes={{
                class: "homepage",
            }}
        />

        <Intro imgSrc={data.bgImg.childImageSharp.fluid} />

        <AboutMe />

        <MainCaseStudy
            imgSrc={data.herminius.childImageSharp.fluid}
            imgAlt="The Embarkation of the Queen of Sheba, Claude Lorrain, 1648"
        />

        <section className="case-studies">
            <CaseStudy
                title="Kheiron Medical"
                description="An interactive microsite to support the virtual attendance to an event"
                imgSrc={data.rsna.childImageSharp.fluid}
                imageAlt="A section of the virtual booth"
                linkText="Visit the site"
                externalUrl="https://rsna.kheironmed.com/"
            />
            <CaseStudy
                title="N8 Research"
                description="A WordPress build for the collaboration of the eight most research intensive Universities in the North of England"
                imgSrc={data.microscopes.childImageSharp.fluid}
                imageAlt="Microscopes"
                linkText="Read more about N8 Research"
                internalUrl="/n8research"
            />
            <CaseStudy
                title="Riverside Spirits"
                description="An Ecommerce site built on WordPress, using Woocommerce, for a local business"
                imgSrc={data.riverside.childImageSharp.fluid}
                imageAlt="Spirit bottle with glass"
                linkText="Visit the site"
                externalUrl="https://riversidespirits.co.uk/"
            />
            <CaseStudy
                title="Jodrell Bank"
                description="Showcasing The Jodrell Bank Observatory"
                imgSrc={data.telescope.childImageSharp.fluid}
                imageAlt="The Lovell Telescope"
                linkText="Read more about Jodrell Bank"
                internalUrl="/jodrell-bank"
            />
            <CaseStudy
                title="Scrolling Video Concept"
                description="Concept of playing a video using the scroll wheel"
                imgSrc={data.smartScroll.childImageSharp.fluid}
                imageAlt="3d view of the iside of the Smart Rope product"
                linkText="View the concept here"
                externalUrl="https://web.pdcx.uk/smart-rope"
            />
            <CaseStudy
                title="Responsive Email Template"
                description="Custom built email template, utilising handlebars, TinyPNG & MailGun"
                imgSrc={data.emailTemplate.childImageSharp.fluid}
                imageAlt="Email inbox screenshot"
                linkText="View on Github"
                externalUrl="https://github.com/jonbrentnall/responsive-email-template"
            />
        </section>
    </main>
);

export const fluidImage = graphql`
    fragment fluidImage on File {
        childImageSharp {
            fluid(quality: 80, maxWidth: 550) {
                ...GatsbyImageSharpFluid_withWebp
            }
        }
    }
`;

export const pageQuery = graphql`
    query {
        bgImg: file(relativePath: { eq: "bg.jpg" }) {
            childImageSharp {
                fluid(quality: 80, maxWidth: 2560) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
        herminius: file(relativePath: { eq: "herminius.jpg" }) {
            childImageSharp {
                fluid(quality: 80, maxWidth: 812) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
        microscopes: file(relativePath: { eq: "microscopes.jpg" }) {
            ...fluidImage
        }
        telescope: file(relativePath: { eq: "telescope.jpg" }) {
            ...fluidImage
        }
        smartScroll: file(relativePath: { eq: "smart-rope-concept.jpg" }) {
            ...fluidImage
        }
        emailTemplate: file(relativePath: { eq: "email-template-banner.jpg" }) {
            ...fluidImage
        }
        riverside: file(relativePath: { eq: "riverside-spirits.jpg" }) {
            ...fluidImage
        }
        rsna: file(relativePath: { eq: "rsna-booth.jpg" }) {
            ...fluidImage
        }
    }
`;
