import React from "react";
import Tilt from "react-tilt";
import { Link } from "gatsby";
import Img from "gatsby-image";
import OnVisible, { setDefaultProps } from "react-on-visible";

setDefaultProps({
    visibleClassName: "in-viewport",
    percent: 40,
});

export default function MainCaseStudy(props) {
    return (
        <section className="main-case-study" id="projects">
            <OnVisible className="main-case-study__title">
                <h3>Projects I've worked on</h3>
            </OnVisible>

            <OnVisible className="main-case-study__text">
                <h3>Herminius</h3>

                <p>
                    A lightweight WordPress website, focusing on speed and
                    performance.
                </p>

                <p>
                    <button>
                        <Link to="/herminius">Read more about Herminius</Link>
                    </button>
                </p>
            </OnVisible>
            <OnVisible className="main-case-study__image">
                <Tilt
                    className="Tilt"
                    options={{ max: 20, glare: true, scale: 1 }}
                >
                    <Img fluid={props.imgSrc} alt={props.imgAlt} />
                </Tilt>
            </OnVisible>
        </section>
    );
}
