import React from "react";
import Particles from "react-particles-js";
import BackgroundImage from "gatsby-background-image";

import Nav from "../components/nav";

if (typeof window !== "undefined") {
    window.addEventListener(
        "scroll",
        function() {
            const scrolled = window.pageYOffset;
            const scrollIcon = document.querySelector(".intro__scroll-icon");

            if (scrollIcon) {
                scrollIcon.style.opacity = "1" - scrolled * 0.004;
            } 
        },
        {
            capture: true,
            passive: true,
        }
    );
}

export default function Intro(props) {
    return (
        <BackgroundImage
            Tag="section"
            className="intro"
            fluid={props.imgSrc}
            backgroundColor={`#040e18`}
            preserveStackingContext={true} //stops gatsby adding 'opacity: 0.99' to the element
        >

        <Particles
            width="100%"
            height="100%"
            className="particles"
            params={{
                particles: {
                    number: {
                        value: 240,
                        density: {
                            enable: true,
                            value_area: 700,
                        },
                    },
                    line_linked: {
                        enable: true,
                        opacity: 0.02,
                    },
                    move: {
                        direction: "right",
                        speed: 0.2,
                    },
                    size: {
                        value: 2,
                    },
                    opacity: {
                        anim: {
                            enable: true,
                            speed: 1.5,
                            opacity_min: 0.05,
                        },
                    },
                },
                retina_detect: true,
            }}
        />

            <div className="intro__title">
                <h1>Jonathan Brentnall</h1>
                <h2>Front end web developer</h2>
            </div>

            <Nav />

            <div className="intro__scroll-icon-container">
                <div className="intro__scroll-icon"></div>
            </div>
        </BackgroundImage>
    );
}
