import React from "react";
import { Link } from "gatsby";
import Img from "gatsby-image";
import OnVisible, { setDefaultProps } from "react-on-visible";

setDefaultProps({
    visibleClassName: "in-viewport",
    percent: 40,
});

export default function CaseStudy(props) {
    let link;

    if (props.internalUrl) {
        link = (
            <button>
                <Link to={props.internalUrl}>{props.linkText}</Link>
            </button>
        );
    } else if (props.externalUrl) {
        link = (
            <button>
                <a
                    href={props.externalUrl}
                    target="_blank"
                    rel="noopener noreferrer"
                >
                    {props.linkText}
                </a>
            </button>
        );
    }

    return ( 
        <div className="case-study__wrapper">
            <h3>{props.title}</h3>
            <OnVisible className="case-study">
                <Img fluid={props.imgSrc} alt={props.imageAlt} />

                <div className="content">

                    {props.description.length > 0 && <p>{props.description}</p>}

                    {link}
                </div>
            </OnVisible>
        </div>
    );
}
